import React, { Component } from 'react';
import { Menu, Modal } from 'semantic-ui-react';

import LoginForm from './LoginForm';
import SignUpForm from './SignUpForm';
import SuccessForm from './SuccessForm';

import { registration, login } from '../../service';

class LoginPopup extends Component {
	state = {
		open: false,
		dimmer: 'blurring',
		isSignUpForm: false,
		isSuccessForm: false,
		formValues: {},
		validateError: false,
	}

	toggleForm = () => this.setState({ isSignUpForm: true, validateError: false });
	show = () => this.setState({ open: true });
	close = () => {
		return this.setState({ open: false, isSignUpForm: false, validateError: false, isSuccessForm: false });
	}
	onChange = (e) => {
		this.setState({ formValues: Object.assign(this.state.formValues, { [e.target.name]: e.target.value }) });
	};

	handleSubmit = (e, action) => {
		e.preventDefault();
		if (action === 'reg') {
			return registration(this.state.formValues).then(res => res.json().then(resParse => {
				if (resParse.error) {
					return this.setState({ validateError: resParse.value });
				} else if (res.status !== 200) {
					return this.setState({ validateError: [resParse] });
				} else {
					this.props.signInObserver(true, resParse);
					return this.setState({ isSuccessForm: 'Ð ÐµÐ³Ð¸ÑÑÑÐ°ÑÐ¸Ñ Ð¿ÑÐ¾ÑÐ»Ð° ÑÑÐ¿ÐµÑÐ½Ð¾!' });
				}
			}));
		}
		if (action === 'login') {
			this.setState({ validateError: false });
			login(this.state.formValues).then(res => res.json().then(res => {
				if (res._id) {
					this.props.signInObserver(true, res);
					this.setState({ isSuccessForm: 'ÐÐ²ÑÐ¾ÑÐ¸Ð·Ð°ÑÐ¸Ñ Ð¿ÑÐ¾ÑÐ»Ð° ÑÑÐ¿ÐµÑÐ½Ð¾!' });
				}
				this.setState({ validateError: res });
			}));
		}
	}

	render () {
		const { open, dimmer, isSignUpForm, isSuccessForm, formValues, validateError } = this.state;

		const loginButton = () => {
			let user = this.props.user;
			if (user) {
				return;
			} else {
				return <Menu.Item name="login" onClick={this.show}>ÐÐ¾Ð¹ÑÐ¸</Menu.Item>;
			}
		};

		const form = () => {
			if (isSuccessForm) {
				return <SuccessForm text={this.state.isSuccessForm} />;
			}
			if (isSignUpForm) {
				return <SignUpForm onChange={this.onChange} handleSubmit={this.handleSubmit} formValues={formValues} validateError={validateError} />;
			} else {
				return <LoginForm onChange={this.onChange} handleSubmit={this.handleSubmit} formValues={formValues} validateError={validateError} />;
			}
		};

		return (
			<div>
				{loginButton()}
				<Modal size={isSignUpForm ? 'tiny' : 'mini'} dimmer={dimmer} open={open} onClose={this.close} closeIcon>
					<Modal.Header className="text-center">{isSignUpForm ? 'Ð ÐµÐ³Ð¸ÑÑÑÐ°ÑÐ¸Ñ' : 'ÐÐ²ÑÐ¾ÑÐ¸Ð·Ð°ÑÐ¸Ñ'}</Modal.Header>
					<Modal.Content>
						<Modal.Description>
							{ form() }
						</Modal.Description>
					</Modal.Content>
					<Modal.Actions>
						{!isSignUpForm &&
							!isSuccessForm &&
							<div className="text-center form-switcher">
								<div>ÐÐµÑ ÑÑÐµÑÐ½Ð¾Ð¹ Ð·Ð°Ð¿Ð¸ÑÐ¸?</div>
								<div> <button className="buttonAsLink" onClick={this.toggleForm}> Ð ÐµÐ³Ð¸ÑÑÑÐ°ÑÐ¸Ñ </button> </div>
							</div>
						}
					</Modal.Actions>
				</Modal>
			</div>
		);
	}
}

export default LoginPopup;
